	}

	.orderStep {
		flex-wrap: wrap;
		margin-bottom: 15px;
	}

	.orderStep li {
		text-align: left;
		width: 100%;
		padding: 0px 0px 10px 25px;
	}

	.orderStep li:before {
		width: 3px;
		height: 100%;
		left: 6px;
	}

	.orderStep li:last-child:before {
		width: 3px;
		display: none;
	}

	.orderStep li:after {
		top: 0px;
		left: 8px;
	}

	.orderStep li.active:last-child:after {
