.navbarToggler {
  width: 28px;
  height: 2px;
  margin: 8px 0px;
  background: #fff;
  display: block;
  position: relative;
  transition: all 0.5s;
}

.navbarToggler:before {
  top: -8px;
}

.navbarToggler:after {
  bottom: -8px;
}

.navbarToggler:before,
.navbarToggler:after {
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  background: #fff;
  left: 0;
  transition: all 0.5s;
}

.dropdown.show .navbarToggler {
  background-color: transparent;
}

.dropdown.show .navbarToggler:before {
  top: 0;
  transform: rotate(45deg);
}

.dropdown.show .navbarToggler:after {
  bottom: 0;
  transform: rotate(-45deg);
}

img.addBanner {
  width: 100%;
}

.navbar-toggler.collapsed {}

.navbar-toggler.collapsed .navbarToggler {
  background-color: #fff;
}

.navbar-toggler.collapsed .navbarToggler:before {
  transform: rotate(45deg);
  top: 0;
  transform: unset;
  top: -8px;
}

.navbar-toggler.collapsed .navbarToggler:after {
  transform: rotate(-45deg);
  bottom: 0;
  transform: unset;
  bottom: -8px;
}

.navbar-toggler {
  padding: 5px;
  border: none;
}

.navbar-toggler .navbarToggler {
