.slick-slide {
  margin: 0 15px;
}

/* the parent */
.slick-list {
  margin: 0 -15px;
}

.slide-arrow {
  font-size: 14px;
  color: #fff;
  border: none;
  position: absolute;
  top: unset;
  bottom: -50px;
  transform: none;
  display: inline-block;
  background-color: transparent;
  display: inline-flex;
  align-items: center;
}

.prev-arrow {
  left: calc(50% - 140px);
}

.next-arrow {
  right: calc(50% - 140px);
}

.prev-arrow:before {
  margin-right: 15px;
}

.next-arrow:after {
  margin-left: 15px;
}

.prev-arrow:before,
.next-arrow:after {
  position: relative;
  content: '';
  display: inline-block;
  width: 80px;
  height: 20px;
  background-size: 80px;
  background-repeat: no-repeat;
}

.prev-arrow:before {
  background-image: url(../images/ArrowLeft.svg);
  background-position: center left;
}

.next-arrow:after {
  background-image: url(../images/ArrowRight.svg);
  background-position: center right;
}

.slick-arrow.slick-disabled {
  opacity: 0.5;
}

.arrowTop_Slide .slide-arrow {
  bottom: unset;
  top: -55px;
}

.arrowTop_Slide .prev-arrow {
  left: calc(82% - 140px);
}

.arrowTop_Slide .next-arrow {
  right: calc(18% - 140px);
}

