.slider {
  /* / height: 250px; / */
  margin: auto;
  position: relative;
  width: 90%;
  /* / display: grid; / */
  place-items: center;
  overflow: hidden;
}

.slideTrack {
  display: flex;
  width: calc(250px * 18);
  animation: scroll 50s linear infinite;
}

.slideTrack:hover {
  animation-play-state: paused;
}

@keyframes scroll {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(calc(-250px * 9));
  }
}

